

.expence-date {
    border: 1px solid rgb(255, 255, 255);
    background-color: rgb(54, 58, 67);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding:0.3rem 1rem;
    border-radius: 12px;
}

.expence-data-month {
    font-size: 0.7rem;
    font-weight: 600;
}

.expence-date-date {
    font-size:1.2rem;
    font-weight: 800;
}

.expence-data-year {
    font-size:0.8rem;
}